.loader{
  position: fixed;
  top: 0;
  left: 0;
  height: 100vh;
  width: 100%;
  z-index: 10000;
  background: #000;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all .3s;
  overflow: hidden;
  &._hide{
    opacity: 0;
    pointer-events: none;
  }
  &__video{
    display: flex;
    align-items: center;
    justify-content: center;
    video{
      width: 100%;
    }
  }
}

@media screen and (max-width: 576px){
  .loader{
    &__video{
      transform: scale(2);
    }
  }
}


@keyframes eAnimation1 {
  from {
    transform: scale(0) translateY(1.75px);
  }
  to{
    transform: scale(1) translateY(1.75px);
  }
}

@keyframes eAnimation2 {
  from {
    transform: scale(0) translateY(1.1px);
  }
  to{
    transform: scale(1) translateY(1.1px);
  }
}

@keyframes scale {
  from {
    transform: scale(0);
  }
  to{
    transform: scale(1);
  }
}

@keyframes fadeTransform {
  from {
    transform: translateY(-40%);
    opacity: 0;
  }
  to{
    transform: translateY(0);
    opacity: 1;
  }
}
