.first-screen{
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  &__logo{
    width: 50px;
    max-width: 100%;
    display: block;
    margin: 0 auto;
  }
  &__slider{
    margin-top: 50px;
    overflow: hidden;
    padding-bottom: 140px;
    position: relative;
  }
  &__avatar{
    position: relative;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(135deg, #4568dc, #af6ab4);
    img{
      border-radius: 50%;
      width: calc(100% - 6px) !important;
      height: calc(100% - 6px);
      object-fit: cover;
    }
  }
  .swiper{
    &-container{
      width: 200px;
      margin: 0 auto;
    }
    &-wrapper{
      align-items: center;
    }
    &-slide{
      height: 200px;
      width: 100%;
      position: relative;
      img{
        width: 100%;
        display: block;
      }
    }
  }
  &__btn{
    position: absolute;
    max-width: calc(100vw - 30px);
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 450px;
    height: 90px;
    border-radius: 35px;
    font-size: 18px;
    margin-top: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    &._active{
      opacity: 1;
      pointer-events: unset;
    }
    strong{
      font-weight: 700;
      margin-left: 7px;
    }
  }
}

.btn-primary.btn-primary-outline{
  background: transparent;
  &:hover{
    background: linear-gradient(135deg, #4568dc, #af6ab4);
  }
}


@media screen and (max-width: 576px){
  .first-screen{
    .swiper{
      &-container{
        width: 150px;
      }
      &-slide{
        height: 150px;
      }
    }
  }
}
