//05-01-2023
.social-links__item{
  width: 40px;
  height: auto;
  &:not(:hover){
    stop{
      stop-color: #fff;
    }
  }
}

.team-info__channels a{
  &:hover{
    fill:#7869C9;
  }
}

.statistic {
  .statistic__item {
    background: $black !important;
    &:hover{
      background: linear-gradient(114.78deg, #4568dc -79.31%, #af6ab4 380.73%), #1e1e20!important;
    }
  }
}

.played-team__title{
  color: #fff;
}

.match-result__top{
  padding: 0 0 15px;
}

.match-result__add-time{
  top: unset;
  bottom: 0;
  color: #fff;
  font-size: 20px;
}

.match-result__captains{
  margin-bottom: -15px;
  &-btn{
    background-color: transparent;
    img{
      width: 30px;
    }
  }
}

.match-result__date{
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

.match-result__status{
  margin-left: 10px;
  img{
    width: 30px;
  }
}

.first-screen{
  &__logo{
    width: 80px;
  }
  .swiper{
    &-slide{
      img{
        transition: all .3s;
      }
      &-next{
        &~.swiper-slide{
          img{
            transform: scale(0.8);
          }
        }
        &+.swiper-slide{
          transform: translateX(-10%);
          &+.swiper-slide{
            transform: translateX(-30%);
            &+.swiper-slide{
              transform: translateX(-50%);
            }
          }
        }
      }
    }
  }
}

.comments{
  &__item{
    &-text{
      position: relative;
      padding-right: 60px;
    }
    &-delete{
      position: absolute;
      right: 10px;
      top: 50%;
      transform: translateY(-50%);
      width: 40px;
      height: 40px;
      border: none;
      background-color: $red;
      border-radius: 5px;
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 0;
      &:hover{
        background-color: darken($red,10%);
      }
      img{
        max-width: 100%;
      }
    }
  }
}

.rounded-start{
  border-bottom-left-radius: 20px !important;
  border-top-left-radius: 20px !important;
}

.rounded-end{
  border-top-right-radius: 20px !important;
  border-bottom-right-radius: 20px !important;
}

table{
  tr{
    border-radius: 5px;
  }
}


@media screen and (max-width: 1700px){
  .played-team__player{
    @media screen and (min-width: 1501px){
      &:nth-child(5) ~ .played-team__player{
        display: none;
      }
    }
  }
}

@media screen and (max-width: 768px){
  .comp-news--mob{
    &>.row{
      &>.col-sm-6{
        display: none;
      }
    }
  }
}
