.transfers{
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 15px;
    margin-top: 30px;
  }
}

.transfer-card{
  display: grid;
  grid-template-columns: 43px 82px calc(50% - (43px + 82px * 4) / 2) 82px 82px calc(50% - (43px + 82px * 4) / 2) 82px;
  align-items: center;
  min-height: 85px;
  border-radius: 30px;
  background: #171717;
  padding: 0 25px;
  &__num{
    padding: 8px;
    background: linear-gradient(135deg, #4568dc, #af6ab4);
    -webkit-text-fill-color: transparent;
    text-fill-color: transparent;
    -webkit-background-clip: text;
    background-clip: text;
    font-weight: 700;
  }
  &__img{
    display: block;
    padding: 8px;
    &-wrap{
      display: block;
      padding-bottom: 100%;
      position: relative;
      background-image: linear-gradient(135deg, #4568dc, #af6ab4);
      border-radius: 50%;
      box-sizing: border-box;
      img{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        border-radius: 50%;
      }
    }
  }
  &__player{
    display: flex;
    align-items: center;
    justify-content: center;
    &-img{
      width: 18px;
      margin-right: 8px;
      flex-shrink: 0;
      svg{
        width: 100%;
        height: auto;
      }
    }
  }
  &__team,
  &__arrow{
    padding: 8px;
    img{
      width: 100%;
    }
  }
  &__player2{
    text-align: center;
    align-items: flex-start;
  }
}


@media screen and (max-width: 1100px){
  .transfer-card{
    padding: 0 15px;
    grid-template-columns: 43px 75px calc(50% - (43px + 75px * 3) / 2) 75px 75px calc(50% - (43px + 75px * 3) / 2);
    &__arrow2{
      display: none;
    }
  }
}


@media screen and (max-width: 992px){
  .transfers{
    &__items{
      gap: 10px;
      margin-top: 20px;
    }
  }

  .transfer-card{
    grid-template-columns: 35px 65px calc(50% - (35px + 65px * 3) / 2) 65px 65px calc(50% - (35px + 65px * 3) / 2);
    &__img,
    &__arrow,
    &__team{
      padding: 4px;
    }
    &__player{
      &-title{
        font-size: 14px;
      }
      &-desc{
        font-size: 12px;
      }
    }
  }
}


@media screen and (max-width: 576px){
  .transfer-card{
    border-radius: 10px;
    padding: 0 5px;
    min-height: 60px;
    grid-template-columns: 20px 40px calc(50% - 60px) 30px 30px calc(50% - 60px);
    &__num{
      padding: 2px;
      font-size: 12px;
    }
    &__arrow,
    &__team{
      padding: 2px;
    }
    &__player{
      &-img{
        display: none;
      }
      &-title{
        font-size: 11px;
        line-height: 100%;
      }
      &-desc{
        font-size: 9px;
      }
    }
  }
}
