.detail-stat__tabs1 li{
  font-size: 24px !important;
}

.detail-stat__tabs2 .m-tabs li{
  font-size: 18px;
  font-weight: 400;
}

.detail-stat{
  .pagination{
    justify-content: flex-end;
    width: calc(100% - 1.5rem);
  }
}

.match-result{
  &__captains-btn{
    background-color: transparent;
    box-shadow: none;
    border: none;
  }
}

.played-team{
  &:nth-child(1){
    .played-team__title{
      text-align: right;
    }
  }
}

.player-card{
  .player-info{
    &:nth-child(1){
      text-align: right;
    }

  }
}

.player-info{
  &__level{
    font-weight: 500;
  }
  &__team{
    font-size: 13px;
  }
}


@media screen and (max-width: 1500px){
  .played-team{
    &:nth-child(1){
      grid-template-columns: calc(100% - 95px) 80px !important;
      .played-team__logo-wrap{
        order: 2;
      }
      .played-team__players{
        justify-content: flex-end;
      }
    }
  }

  .player-card{
    .player-info{
      &:nth-child(1){
        flex-direction: row-reverse;
      }
    }
  }
}


@media screen and (max-width: 1200px){
  .played-team{
    &:nth-child(1){
      grid-template-columns: calc(100% - 65px) 50px !important;
    }
  }
}


@media screen and (max-width: 767px){
  .detail-stat{
    .pagination{
      justify-content: center;
    }
  }
}


@media screen and (max-width: 576px){
  .played-team{
    &:nth-child(1){
      grid-template-columns: calc(100% - 65px) 50px !important;
      .played-team__logo-wrap{
        order: -1;
        margin-left: auto;
      }
    }
  }

  .player-card{
    .player-info{
      &:nth-child(1){
        .player-info__images{
          margin-left: auto;
        }
        .player-info__assist{
          right: unset;
          left: 0;
        }
      }
    }
  }

  .stat-row{
    .player-info__assist{
      position: static;
    }
  }

  .result{
    &__score{
      font-size: 36px;
    }
    &__date{
      font-size: 18px;
    }
  }

  .admin-stat__title{
    font-size: 14px;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 100%;
    width: 100%;
  }
}
