.search-players{
  background-image: unset !important;
  padding-bottom: 1.5em !important;
  &:before{
    display: none;
  }
  &__grid{
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 64px / 3));
    gap: 32px;
  }
  .team-player-cards__item{
    width: 100%;
    margin: 0;
    min-height: unset;
  }
}

.free-dropdown{
  width: 480px;
  max-width: 100%;
  position: relative;
  &._active{
    .free-dropdown__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 48px;
    background: linear-gradient(135deg, #4568dc, #af6ab4);
    border-radius: 8px;
    position: relative;
    z-index: 4;
    cursor: pointer;
    svg{
      position: absolute;
      top: 50%;
      right: 5px;
      transform: translateY(-50%);
      width: 40px;
      height: auto;
    }
    path{
      fill: #fff;
    }
  }
  &__img{
    position: absolute;
    width: 55px;
    left: 16px;
    top: 50%;
    transform: translateY(-50%);
  }
  &__title{
    font-weight: 700;
    font-size: 24px;
  }
  &__hidden{
    transform: all .3s;
    opacity: 0;
    pointer-events: none;
    position: absolute;
    background: #343b41;
    max-width: 100%;
    width: 270px;
    top: 100%;
    right: 0;
    padding: 18px 8px 8px;
    border-radius: 0 0 8px 8px;
    margin-top: -10px;
    z-index: 3;
  }
  &__badge{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 40px;
    padding: 0 5px;
    background: linear-gradient(135deg, #4568dc, #af6ab4);
    color: #fff;
    text-transform: uppercase;
    font-size: 16px;
    font-weight: 700;
    border-radius: 10px;
    margin-top: 12px;
  }
  &__items{
    display: grid;
    grid-template-columns: 100%;
    gap: 12px;
  }
  &__item{
    display: flex;
    align-items: center;
    font-size: 14px;
    line-height: 120%;
    img{
      width: 40px;
      margin-right: 8px;
      flex-shrink: 0;
    }
    svg{
      width: 40px;
      height: auto;
      flex-shrink: 0;
      margin-left: auto;
      margin-right: -8px;
    }
    path{
      fill: #202529;
    }
    &._active{
      path{
        fill: #fff;
      }
    }
  }
}

.free-dropdown__badge{
  margin-top: 20px;
}

.free-card .team-player-cards__nickname{
  font-weight: 600;
}

.free-card{
  background: #171717;
  border-radius: 20px;
  padding: 15px 15px 45px;
  position: relative;
  .team-player-cards__nickname{
    height: 30px;
    border-radius: 0 0 20px 20px;
    font-size: 16px;
    padding: 0 20px;
  }
  &__time{
    display: flex;
    align-items: center;
    font-weight: 600;
    font-size: 16px;
    line-height: 120%;
    i{
      margin-right: 15px;
      flex-shrink: 0;
      width: 18px;
      height: 18px;
      border-radius: 50%;
    }
  }
  &__row{
    margin-top: 10px;
    display: flex;
    align-items: center;
  }
  &__num{
    color: $blue;
    font-size: 14px;
    margin-right: 20px;
  }
  &__avatar{
    width: 60px;
    height: 60px;
    display: block;
    position: relative;
    flex-shrink: 0;
    margin-right: 30px;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
      display: block;
    }
    .fi{
      width: 16px;
      height: 16px;
      display: block;
      right: 0;
      left: unset !important;
      bottom: 0;
      &:before{
        content: '';
      }
    }
  }
  &__info{
    display: flex;
    align-items: center;
    &-icon{
      width: 16px;
      flex-shrink: 0;
      margin-right: 6px;
      svg{
        width: 100%;
        height: auto;
      }
    }
  }
  &__console{
    margin-top: 4px;
    display: flex;
    align-items: center;
    font-size: 14px;
    color: #73787b;
  }
  &__positions{
    width: 165px;
    padding-left: 15px;
    margin-left: auto;
    margin-top: -6px;
    &-grid{
      display: flex;
      justify-content: center;
      align-self: center;
      flex-wrap: wrap;
      width: calc(100% + 16px);
    }
    img{
      margin: 6px 16px 0 0;
      width: calc(100% / 3 - 16px);
      display: block;
    }
  }
}



@media screen and (max-width: 1500px){
  .search-players{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 16px));
    }
  }
}


@media screen and (max-width: 1200px){
  .search-players{
    &__grid{
      grid-template-columns: repeat(2,calc(50% - 8px));
      gap: 30px 16px;
    }
  }
  .free-card{
    &__avatar{
      margin-right: 10px;
    }
    &__positions{
      &-grid{
        width: calc(100% + 5px);
      }
      img{
        margin: 5px 5px 0 0;
        width: calc(100% / 3 - 5px);
      }
    }
  }
}


@media screen and (max-width: 1100px){
  .search-players{
    &__grid{
      grid-template-columns: 100%;
    }
  }
}


@media screen and (max-width: 576px){
  .free-card{
    &__num{
      margin-right: 10px;
    }
    &__avatar{
      width: 45px;
      height: 45px;
    }
    &__position{
      flex-shrink: 0;
    }
    &__name{
      font-size: 14px;
    }
    &__console{
      font-size: 12px;
    }
  }
}
