.result__logo{
  margin: 0 auto;
  &-text{
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 10px;
    text-align: center;
    svg{
      margin-right: 10px;
    }
  }
}


@media screen and (max-width: 576px) {
  .result__logo{
    &-wrap{
      width: 90px;
    }
    &-text{
      flex-direction: column-reverse;
      font-size: 13px;
      padding: 0 3px;
      svg{
        margin: 5px 0 0;
      }
    }
  }
}

@media screen and (max-width: 450px){
  .result{
    align-items: unset;
  }
}
