.best-card{
  position: relative;
  &__position{
    position: absolute;
    right: 0;
    bottom: 100%;
    margin-bottom: 8px;
  }
}

.social-links__item{
  width: 35px;
  height: 35px;
  position: relative;
  &:hover{
    .social-links__item-img1{
      opacity: 0;
    }
    .social-links__item-img2{
      opacity: 1;
    }
  }
  img{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
    width: 100%;
    transition: all .3s;
  }
  &-img2{
    opacity: 0;
  }
}
