.team-player-cards__item{
  padding-bottom: 55px;
  overflow: hidden;
}

.team-player-cards__nickname{
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 40px;
  display: flex;
  align-items: center;
  padding: 0 30px;
  background: #000;
}

.team-player-cards__bottom{
  bottom: -30px;
}

.card-news{
  overflow: hidden;
}

.card-news-inner_block{
  border-radius: 0 !important;
  width: calc(100% + 10px) !important;
  height: calc(100% + 10px) !important;
  padding: 10px 0 0 10px;
  margin: -10px 0 0 -10px;

}

.card-news-tall .card-news-inner_block{
  &:before{
    content: '';
    position: absolute;
    background: #000;
    bottom: 0;
    left: 0;
    width: 110%;
    height: 30px;
    margin-left: -5%;
    pointer-events: none;
  }
  .row{
    position: relative;
    z-index: 2;
  }
}

.page-news-banner{
  height: unset;
  &-inner_block{
    min-height: 260px;
  }
}


.tournament-carousel__item{
  &:nth-child(even){
    .tournament-carousel__item-block img{
      margin-bottom: 10px;
    }
  }
}


@media screen and (max-width: 576px){
  .matches-results-wrap {
    padding: 20px 40px;
    height: 136px;
    .swiper{
      width: 100%;
    }
    .swiper-button-prev{
      left: 0;
    }
    .swiper-button-next{
      right: 0;
    }
  }
  .matches-results__item{
    margin-right: 0;
  }

  .tabs_default{
    overflow-x: auto;
  }

  .popular-hall__item-wrap-header{
    display: block;
    h2{
      margin-bottom: 10px;
    }
  }
}


.card-news{
  border-radius: 10px !important;
}

#content{
  background-image: url(/images/site-bg.png);
  background-position: center bottom;
}


@media screen and (max-width: 576px){
  .matches-results-wrap {
    .swiper-button{
      background-repeat: no-repeat !important;
      background-position: center;
      background-image: url("data:image/svg+xml;charset=utf8,%3Csvg width='6' height='9' viewBox='0 0 6 9' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1.06378 1.4043L4.93612 4.72345L1.06378 8.04259' stroke='white' stroke-linecap='round' stroke-linejoin='round'/%3E%3C/svg%3E") !important;
    }
  }

}
